import React, {useEffect} from 'react';

import { useSelector } from 'react-redux';

import 'bootstrap/dist/css/bootstrap.min.css';
import { Link } from 'react-router-dom';

import { useDispatch } from 'react-redux';
import { getPosts } from '../actions/posts';
/*
const submitScore = ()=> {
    let name = document.getElementById("textArea").value;
    // submit name and score
}
*/
const Leaderboard = (props) => {
    const dispatch = useDispatch();
    useEffect(
        ()=> {dispatch(getPosts())},
         [dispatch]);
    const posts = useSelector((state) => state.posts);
    const user = JSON.parse(localStorage.getItem('profile'));
    console.log(posts);
    let filtered = new Array(10);
    const comp = new Array(10);
    for(var i = 0; i < 10; i++){
        filtered[i] = new Array(3);
        if(i >= posts.length){
            filtered[i][0] = "";
            filtered[i][1] = "";
            filtered[i][2] = "";

        }else{
            filtered[i][0] = posts[i]["name"]+":";
            filtered[i][1] = posts[i]["score"];
            filtered[i][2] = "("+(i+1)+")";
            if(user){
                if(user.result.name + ":" === filtered[i][0]){
                console.log(i + 'not the if statement');
                comp[i] = <div><h6 className = "green-rock">  {filtered[i][2]} &nbsp; &nbsp; </h6> <h6 className = "green-rock"> {filtered[i][0]} {filtered[i][1]}</h6> </div>;
                }
                else{
                    console.log("here" + user.result.name + "  " + filtered[i][0]);
                    comp[i] = <div><h6>  {filtered[i][2]} &nbsp; &nbsp; </h6><h6> {filtered[i][0]} {filtered[i][1]}</h6> </div>;
                }
            }else{
                console.log("no, here");
                comp[i] = <div><h6>  {filtered[i][2]} &nbsp; &nbsp; </h6><h6> {filtered[i][0]} {filtered[i][1]}</h6> </div>;
            }
        }
    }
    return (
        <div id="background" style={{width: '100vw', height:100+'%', overflowX:'hidden'}}>
            <div className = "leaderboard-title" style={{marginTop: '50px', textAlign: 'center'}}>
                <h1>Leaderboard</h1> <br></br>
            </div>
            <div className="container-fluid">
            <div className="row">
                <div className="col-12" style={{textAlign: 'center'}}>
                    {comp}
                </div>
                
            </div>
            </div>
           <div className="row">
                <div className="col-12" style={{textAlign:'center'}}>
                    <button style={{ marginTop: '4vw'}}><Link  style={{fontSize: 'inherit', fontFamily: 'inherit', textDecoration: 'inherit', color: 'inherit'}} to="/react-hero">Go Back</Link></button>
                </div>
            </div>
        </div>
        
    );  
}

export default Leaderboard;