import React, {useState, useEffect} from 'react';
import { Link } from 'react-router-dom';
import { useDispatch } from 'react-redux';
import { useHistory } from 'react-router-dom';
import 'bootstrap/dist/css/bootstrap.min.css';
const Navbar = (props) => {

    //const user = null;

    //state field setup for non-class classes
        //first item is the field, second is the function that sets it
        // we instantly set it to the user stored in local storage
    const [user, setUser] = useState(JSON.parse(localStorage.getItem('profile')));
    const dispatch = useDispatch();
    const history = useHistory();
    useEffect(() => {
        const token = user?.token;
        setUser(JSON.parse(localStorage.getItem('profile')));
        
    }, []);
    const logout = () => {
        dispatch({ type: 'LOGOUT'});
        history.push("/react-hero");
        localStorage.setItem('profile', null);
        setUser(null);
    }


    return (
        <nav className="navbar navbar-expand navbar-dark">
            <div className="container-fluid">
                        {user ? 
                        (<ul className="navbar-nav ms-auto me-4 mb-2 mb-lg-0">
                            <li className="nav-item">
                                <a className="nav-link">{user.result.name}</a>
                            </li>
                            <li className="nav-item">
                                <a className="nav-link" onClick = {logout}>Logout</a>
                            </li> 
                        </ul>) : 
                        (<ul className="navbar-nav ms-auto me-4 mb-2 mb-lg-0">
                            <li className="nav-item">
                                <Link className = "nav-link" style={{ fontSize: 'inherit', fontFamily: 'inherit', textDecoration: 'inherit', color: 'inherit'}} to="/react-hero/login">Login </Link>
                            </li>
                            <li className="nav-item">
                            <Link className = "nav-link" style={{ fontSize: 'inherit', fontFamily: 'inherit', textDecoration: 'inherit', color: 'inherit'}} to="/react-hero/create">Sign Up</Link>
                            </li> 
                        </ul>) }; 
                </div>
        </nav>
    );    
}

export default Navbar;